import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { DataTransferService } from 'src/app/services/data-transfer.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { URLS } from 'src/assets/constant';

@Component({
  selector: 'app-award-list',
  templateUrl: './award-list.page.html',
  styleUrls: ['./award-list.page.scss'],
})
export class AwardListPage implements OnInit {
  Data: any=[];
  limit:any="0";
  myInfiniteScroll:any;
  myinfiniteScrollCall:boolean=false;
  errorMessage: string='';
  awdDetailData: any;
  filterRes: any;
  constructor(private router:Router,private localStorage:LocalStorageService,private apiservices:ApiservicesService,
    private zone:NgZone,private sendData:DataTransferService) { }

  ngOnInit() {
  
  }
  ionViewWillEnter(){
    this.filterRes=this.sendData.alldata;
    console.log('val',this.filterRes);
    this.awardListFun();
  }

  awardDetail(item){
    this.sendData.myParam=item;
    this.router.navigate(['/award-detail']);
    // this.router.navigate(['/award-dashboard']);
  }
  awardListFun(){
    this.zone.run(()=>{
      this.apiservices.showLoader();
      this.localStorage.getStorage().then(stored=>{
        let apiKey={  
          limit:this.limit,
          employee_id:stored.employeeID,
          award_id: this.filterRes.award_id,
          award_type_id:this.filterRes.award_type_id,
          department_id:this.filterRes.department_id,
          location_id:this.filterRes.location_id,
          };
      console.log("resend OTP by Voice Call, api key", apiKey);
      this.apiservices.apiCallWithLoginToken(URLS.nominationListApi, apiKey).subscribe((result) => {
        console.log("resend OTP by Voice Call, api response==",result);
        this.apiservices.hideLoader();
        this.awdDetailData=result;
        if(result.status==1 || result.status=='1'){
          if(this.Data==undefined){
            this.Data=result.data.list_data;
          }else{
            this.Data=this.Data.concat(result.data.list_data);
          }
          if(this.myinfiniteScrollCall==true){
            // console.log("noticeBoard List Infinite Scroll if2==", this.communicationList);
            this.myInfiniteScroll.target.complete();
            this.errorMessage = "";
            this.myinfiniteScrollCall=false;
          }
          // this.apiservices.showToastMessage(result.message, 'top', 3000, 'greenBg');
        }else{
          this.errorMessage = result.message;
          this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
          if(this.myinfiniteScrollCall==true){
            this.myInfiniteScroll.target.complete();
            this.myinfiniteScrollCall=false;
          }
        }
      },err=>{
        this.apiservices.hideLoader();
        this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
      });
      });
    });


  }

  loadData(infiniteScroll){
    this.myinfiniteScrollCall=true;
    console.log("Begin async operation 1==");
    setTimeout(() => {
      this.limit= this.Data.length;
      console.log("Begin async operation 2==",this.limit);
      this.myInfiniteScroll = infiniteScroll;
      this.awardListFun();
    }, 500);
  }

  FilterFun(){
    this.sendData.alldata=this.filterRes;
    this.router.navigate(['/filter-list']);
  }

}
